import React,{PureComponent} from 'react';

import UcSwiper from "../components/uc-swiper";


// 解构路由组件
//BrowserRouter   约定模式 为 history
//HashRouter     约定模式 为 hash
//Route        匹配、展示
// NavLink    声明式跳转 还可以约定 路由激活状态
//Link        声明式跳转   无激活状态
// ------Redirect   重定向
//from来自
//to 去向
//-----------
//Switch   排他性匹配

//**Route**属性
//path 路径
//component 在地址匹配的时候React的组件才会被渲染
import '../assets/css/home.css'

import {NavLink, Route, Switch, Redirect, BrowserRouter, withRouter, Link} from 'react-router-dom';

import BareScreen from "../components/BareScreen";
import UcList from "../components/uc-list";

import Nnnnn from "./Nnnnn";
import Two from "./two";
import Backup from "./Backup";


import {connect} from "react-redux";

import {clear,updateList} from "../store/actionCreators";

import { Carousel, WingBlank } from 'antd-mobile';

class Home extends PureComponent{
	constructor() {
		super();
		this.timerID=null;
	}

	state={

		str :["儿童劫皮肤", "小学生诺手", "无敌托儿索"],
		i:0,

		imgHeight: 3.5,
	}
    componentDidMount(){

		//在componentDidMount，进行scroll事件的注册，绑定一个函数，让这个函数进行监听处理
        window.addEventListener('scroll', this.bindHandleScroll)
		// 添加定时器
		// this.timerID = setInterval(
		// 	() => this.tick(),
		// 	2000
		// );

        //读取数据
		// React.axios.all([
		// 	React.axios({url:'/api/goods/banner',params:{_page:1,_limit:3}}),
		// 	React.axios({url:'/api/goods/home',params:{_page:1,_limit:12}})
		// ]).then(React.axios.spread((banner,home)=>{
		// 	banner.data.data.map(item=>item.banner=this.baseUrl+'/'+item.banner)
		//
		// 	this.setState({
		// 	banner:banner.data.data,
		// 	home:home.data.data
		// 	})
		// }))
		this.props.dispatch(
			updateList({collectionName:'home',type:'UPDATE_HOME',params:{_page:1,_limit:12}})
		)
		this.props.dispatch(
			updateList({collectionName:'banner',type:'UPDATE_BANNER',params:{_page:1,_limit:3}})
		)










		// this.axios({url:'/api/goods/home',params:{_page:1,_limit:10}}).then(res=>console.log(res));

        // let res = await this.axios({url:'/api/goods/home',params:{_page:1,_limit:10}});

        // this.setState({banner:res.data.data})

        // axios({url:'/mock/home',params:{_page:1,_limit:5}}).then(res=>console.log('mock',res))

    }

	// 输入框的内容定时切换
	tick=()=>{
		this.state.i++;
		if(this.state.i==2){
			this.state.i=0
		}
		this.setState({
			i:this.state.i
		})
		this.chang.setAttribute('placeholder',this.state.str[this.state.i])
	}





    bindHandleScroll=(event)=>{
        // 滚动的高度
        const scrollTop = (event.srcElement ? event.srcElement.documentElement.scrollTop : false)
            || window.pageYOffset
            || (event.srcElement ? event.srcElement.body.scrollTop : 0);
        // 判断用户当前是否进行了横向滚动，如果用户发生了横向滚动，则设置元素为static
        // const scrollLeft = (event.srcElement ? event.srcElement.documentElement.scrollLeft : false)
        //     || window.pageXOffset
        //     || (event.srcElement ? event.srcElement.body.scrollLeft : 0);
		if(this.headerdom){
			if(scrollTop<=180){
				this.headerdom.style.cssText='opacity:1;background:null;'
			}else if(scrollTop>180&&scrollTop<=500){
				this.headerdom.style.opacity='0'
			}else if(scrollTop>500){
				this.headerdom.style.cssText='opacity:1;background:#fff;';
			}else{
				this.headerdom.style.cssText='opacity:1;background:null;'
			}
		}
    }
    //在componentWillUnmount，进行scroll事件的注销
    componentWillUnmount(){
        window.removeEventListener('scroll', this.bindHandleScroll);
		clearInterval(this.timerID);
    }
	renderPage=()=>{
		let {banner,home,match}=this.props;
		return	(
			<>
				<div className="header"  ref={el=>this.headerdom=el}>
					<span onClick={()=>this.props.history.push('/nnnnn')} className="iconfont icon-fdj"></span>
					<input type="text" ref={el=>this.chang=el}  onClick={()=>this.props.history.push('/soso')} placeholder="西安泡馍"/>
					<span onClick={()=>this.props.history.push('/nnnnn')} className="iconfont icon-ht"></span>
					<span onClick={()=>this.props.history.push('/nnnnn')} className="iconfont icon-gwc"></span>
				</div>
					{/*<Carousel*/}
					{/*	dotStyle={{width:'0.4rem',height:'0.1rem',borderRadius:"0"}}*/}
					{/*	dotActiveStyle={{background:"blue",width:'0.4rem',height:'0.1rem',borderRadius:"0"}}*/}
					{/*	autoplay={true}*/}
					{/*	infinite*/}
					{/*>*/}
					{/*	{banner.map(val => (*/}
					{/*		<Link*/}
					{/*			key={val}*/}
					{/*			to={'/detail/' + val._id + '?apiname=banner'}*/}
					{/*			style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight+'rem' }}*/}
					{/*		>*/}
					{/*			<img*/}
					{/*				src={`${React.baseUrl}/${val.banner}`}*/}
					{/*				alt=""*/}
					{/*				style={{ width: '100%',verticalAlign: 'top' }}*/}
					{/*			/>*/}
					{/*		</Link>*/}
					{/*	))}*/}
					{/*</Carousel>*/}


				<UcSwiper
					data={banner}
					to={{pathname:'/detail',apiname:'banner'}}
					style={{height:'3.5rem'}}
				/>
				<div className={'Section'}>
					{/*src={require('../assets/img/say.png')}*/}
					<a onClick={()=>this.props.history.push('/nnnnn')}><img src={require('../assets/img/x1.png')} alt=""/>机票</a>
					<a onClick={()=>this.props.history.push('/nnnnn')}><img src={require('../assets/img/x2.png')} alt=""/>酒店</a>
					<a onClick={()=>this.props.history.push('/nnnnn')}><img src={require('../assets/img/x3.png')} alt=""/>火车票</a>
					<a onClick={()=>this.props.history.push('/nnnnn')}><img src={require('../assets/img/x4.png')} alt=""/>汽车票</a>
					<a onClick={()=>this.props.history.push('/nnnnn')}><img src={require('../assets/img/x5.png')} alt=""/>门票</a>

				</div>
				<div className={'tuijian'}>
					{
						[1,2,3,4,5,6,7,8].map((item,index)=>(
							<div key={index} onClick={()=>this.props.history.push('/nnnnn')} className={'tjlist'}>
								<p>出境游</p>
								<span>走遍全球</span>
							</div>
						))
					}

				</div>
				<div className={'vip'}>
					<div onClick={()=>this.props.history.push('/nnnnn')} className={'vipleft'}>
						<strong>&nbsp;&nbsp;我的会员&nbsp;&nbsp;</strong>
						<span>抢限量福利&nbsp;&nbsp;</span>
					</div>
					<div onClick={()=>this.props.history.push('/nnnnn')} className={'vipright'}>
						<strong>&nbsp;&nbsp;签到&nbsp;&nbsp;</strong>
						<span>领里程&nbsp;&nbsp;</span>
					</div>

				</div>

				<div className={'nav'}>
					<NavLink activeClassName='home__nav--active' to={`${match.url}/index`}>西安</NavLink>
					<NavLink activeClassName='home__nav--active' to={`${match.url}/nnnnn`}>宝鸡</NavLink>
					<NavLink activeClassName='home__nav--active' to={`${match.url}/beijing`}>北京</NavLink>
					<NavLink activeClassName='home__nav--active' to={`${match.url}/shanghai`}>上海</NavLink>
					<NavLink activeClassName='home__nav--active' to={`${match.url}/sanya`}>三亚</NavLink>
					<NavLink activeClassName='home__nav--active' to={`${match.url}/zhengzhou`}>郑州</NavLink>
					<NavLink activeClassName='home__nav--active' to={`${match.url}/shenzheng`}>深圳</NavLink>
					<NavLink activeClassName='home__nav--active' to={`${match.url}/guangzhou`}>广州</NavLink>
					<NavLink activeClassName='home__nav--active' to={`${match.url}/aaa`}>三亚</NavLink>
					<NavLink activeClassName='home__nav--active' to={`${match.url}/cs`}>郑州</NavLink>
					<NavLink activeClassName='home__nav--active' to={`${match.url}/shescasnzheng`}>深圳</NavLink>
					<NavLink activeClassName='home__nav--active' to={`${match.url}/guangzhxsou`}>广州</NavLink>
				</div>

				{/*<Route exact path={'/home'} component={Backup}/>*/}
				{/*<Route  path={'/home/nnnnn'} component={Nnnnn}/>*/}
				<Redirect path={'/home'} to={'/home/index'} />
				{/*<Route path={`${match.url}`} component={Backup}></Route>*/}
				<Route  path={`${match.path}`} component={Backup}></Route>
				
			</>
	)}
	renderBareScreen=()=>(<BareScreen/>)
	render(){
		// console.log('home',this.state.home)
		  let el=null;
		  if (this.props.home) {
		    el = this.renderPage()
		  }else{
		    // el = this.renderBareScreen()
			  el = null
		  }
		  return el;
	}

}

export default connect(
	state=>({home:state.home,banner:state.banner})
)(Home)